body {
  background: url("./images/office.jpeg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

button {
  display: inline-block;
  padding: 0.75rem 1.25rem;
  border-radius: 10rem;
  color: rgb(10, 10, 10);
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.15rem;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
  background-color: rgb(216, 54, 102);
}

.cont {
  flex-direction: column;
  display: flex;
  align-items: center;
  color: rgb(5, 4, 4);
  font-size: 1em;
  background-color: rgb(236, 228, 228);
  max-width: 50%;
  text-align: center;
  border-radius: 50%;
  padding: 2em;
}

.all {
  flex-direction: column;
  display: flex;
  align-items: center;
  color: rgb(5, 4, 4);
  justify-content: center;
  box-shadow: none;
}

.buttons {
  display: flex;
  flex-direction: column;
}
